<script>
import '../assets/css/main.css'
import axios from 'axios'

    export default {
        name : 'Main',
        data(){
        return{
        ftreatments: null,
        btreatments: null,
        bitreatments: null
        }
        },
         mounted(){
        axios.get('http://backend.victoriousskinclinic.com/api/treatment')
        .then(res=>res.data.data)
        .then(data=>{
          // console.log(data)
          let tre = data.filter(x => x.body_part == 'Face');
          // console.log(tre);
          let tre1 = data.filter(y => y.body_part == 'Body');
          // console.log(tre1);
          let tre2 = data.filter(z => z.body_part == 'Body Injection');
          // console.log(tre2);
          this.ftreatments = tre;
          this.btreatments = tre1;
          this.bitreatments = tre2;
        })
        },
        methods: {
        // treat_view(id) {
           
        // }
        },
    }

</script>

