<template>
    <Nav></Nav>
    <div class="mt-4">
           <!-- ======= Book A Table Section ======= -->
    <section id="book-a-table" class="book-a-table  booking">
      <div class="container" data-aos="fade-up">

        <div class="section-header">
          <h2>Book A Treatment</h2>
          <p>Book <span>Your Treatment</span> With Us</p>
        </div>


                 <span class="offset-5">Enter your phone number</span>
                <div class="d-flex justify-content-center px-5"> 
                  
                  <div class="search"> 
                  <input type="text" id="phsearch" class="search-input" placeholder="Search your appointment..." name=""> 
                  <a href="#" class="search-icon" @click="search()"> <i class="fa fa-search"></i> 
                </a> </div> </div>
             

        
        <div class="row g-0">
            <div class="contentcalendar">
    <div class="calendar-container">
      <div class="calendar"> 
        <div class="year-header"> 
          <span class="left-button" id="prev"> &lang; </span> 
          <span class="year" id="label"></span> 
          <span class="right-button" id="next"> &rang; </span>
        </div> 
        <table class="months-table"> 
          <tbody>
            <tr class="months-row">
              <td class="month">Jan</td> 
              <td class="month">Feb</td> 
              <td class="month">Mar</td> 
              <td class="month">Apr</td> 
              <td class="month">May</td> 
              <td class="month">Jun</td> 
              <td class="month">Jul</td>
              <td class="month">Aug</td> 
              <td class="month">Sep</td> 
              <td class="month">Oct</td>          
              <td class="month">Nov</td>
              <td class="month">Dec</td>
            </tr>
          </tbody>
        </table> 
        
        <table class="days-table"> 
          <td class="day">Sun</td> 
          <td class="day">Mon</td> 
          <td class="day">Tue</td> 
          <td class="day">Wed</td> 
          <td class="day">Thu</td> 
          <td class="day">Fri</td> 
          <td class="day">Sat</td>
        </table> 
        <div class="frame"> 
          <table class="dates-table"> 
              <tbody class="tbody">             
              </tbody> 
          </table>
        </div> 
        <button class="buttoncalendar" id="add-button">Add Treatment</button>
      </div>
    </div>
    <div class="events-container">
        
    </div>
    <div class="dialog" id="dialog">
        <h2 class="dialog-header"> Add New Treatment </h2>
        <form class="form" id="form">
          <div class="form-container" align="center">
            <div class="row">
                <div class="col-md-6">
                  <label class="form-label" id="valueFromMyButton" for="name">Enter Name</label>
                  <input class="input" type="text" id="name" maxlength="36">
                </div>
                <div class="col-md-6">
                  <label class="form-label" id="valueFromMyButton" for="count">Phone No.</label>
                  <input class="input" type="number" id="phone" min="0" max="1000000" maxlength="15">
                </div>
            </div>
            <div class="row">
                <div class="col-md-6">
                  <label class="form-label" id="valueFromMyButton" for="count">Choose Treatment</label>
                  <select id="treat" name="treat" class="input" >
                      <option value=""></option>
                      <option value="Face">Face</option>
                  </select>
                </div>
                <div class="col-md-6">
                  <label class="form-label" id="valueFromMyButton" for="count">Select Doctor</label>
                  <select id="doctor" name="doctor" class="input" >
                      <option value=""></option>
                      <option value="Dr.Haymar">Dr.Haymar</option>
                  </select>
                </div>
            </div>
          
            <label class="form-label" id="valueFromMyButton" for="count">Time</label>
            <input class="input" type="time" id="booktime"/>
            <input type="button" value="Cancel" class="buttoncalendar" id="cancel-button">
            <input type="button" value="OK" class="buttoncalendar" id="ok-button">
          </div>
        
        </form>
      </div>
  </div>
          

        </div>

      </div>
    </section>
    <!-- End Book A Table Section -->

    <Footer></Footer>
    </div>
</template>
<script>

import Footer from '@/components/Footer.vue';
import Nav from '@/components/Nav.vue';
import 'bootstrap/dist/css/bootstrap.min.css'
import 'bootstrap/dist/js/bootstrap.bundle.min.js'
import 'jquery/dist/jquery.min.js'
import 'jquery/dist/jquery.js'
import axios from 'axios'

    export default {
    name: "Booking",
    components: {  Footer,Nav },
     data() {
      return {
        name : '',
        phone : '',
        doctor : '',
        time : '',
        date : ''
      }
    }, 
    methods: {
      search(){
        let val = $('#phsearch').val();
          if(val != ''){
        axios.get('http://backend.victoriousskinclinic.com/api/appointment')
        .then(res=>res.data.data)
        .then(res=>{
            res.forEach(element => {
                if(element.phone == val){
                    this.name = element.patient_name;
                    this.phone = val;
                    this.doctor = element.doctor_name;
                    this.time = element.time;
                    this.date = element.date;
                     $(".events-container").empty();
                    let event_card = $("<div class='event-card'></div>");
                    let event_name = $("<div class='event-name'>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; Your Appointment Information <br>&nbsp; Patient Name :"+this.name+".<br>&nbsp;&nbsp;Phone No :"+val+".<br>&nbsp;&nbsp;Doctor Name :"+this.doctor+".<br>&nbsp;&nbsp;<Appintment Date :"+this.date+".<br>&nbspAppointment Time :"+this.time+"</div>");
                    $(event_card).css({ "border-left": "10px solid #FF1744" });
                    $(event_card).append(event_name);
                    $(".events-container").append(event_card);
                }
                $('#phsearch').val('');
            });
        })

        } 
      }
    }, 
    mounted(){

// console.log(event_data);

        // Given data for events in JSON format
var event_data = {
    "events": [
    {
        // "occasion": " Repeated Test Event ",
        // "invited_count": 120,
        "year": 2022,
        "month": 5,
        "day": 11,
        // "cancelled": true
    },

    ]

};
      // Setup the calendar with the current date
$(document).ready(function(){
    var date = new Date();
    var today = date.getDate();
    // Set click handlers for DOM elements
    $(".right-button").click({date: date}, next_year);
    $(".left-button").click({date: date}, prev_year);
    $(".month").click({date: date}, month_click);
    $("#add-button").click({date: date}, new_event);
    // Set current month as active
    $(".months-row").children().eq(date.getMonth()).addClass("active-month");
    init_calendar(date);
    var events = check_events(today, date.getMonth()+1, date.getFullYear());
    show_events(events, months[date.getMonth()], today);
});


// Initialize the calendar by appending the HTML dates
function init_calendar(date) {
    $(".tbody").empty();
    $(".events-container").empty();
    var calendar_days = $(".tbody");
    var month = date.getMonth();
    var year = date.getFullYear();
    var day_count = days_in_month(month, year);
    var row = $("<tr class='table-row'></tr>");
    var today = date.getDate();
    // Set date to 1 to find the first day of the month
    date.setDate(1);
    var first_day = date.getDay();
    // 35+firstDay is the number of date elements to be added to the dates table
    // 35 is from (7 days in a week) * (up to 5 rows of dates in a month)
    for(var i=0; i<35+first_day; i++) {
        // Since some of the elements will be blank, 
        // need to calculate actual date from index
        var day = i-first_day+1;
        // If it is a sunday, make a new row
        if(i%7===0) {
            calendar_days.append(row);
            row = $("<tr class='table-row'></tr>");
        }
        // if current index isn't a day in this month, make it blank
        if(i < first_day || day > day_count) {
            var curr_date = $("<td class='table-date nil'>"+"</td>");
            row.append(curr_date);
        }   
        else {
            var curr_date = $("<td class='table-date'>"+day+"</td>");
            var events = check_events(day, month+1, year);
            if(today===day && $(".active-date").length===0) {
                curr_date.addClass("active-date");
                show_events(events, months[month], day);
            }
            // If this date has any events, style it with .event-date
            if(events.length!==0) {
                curr_date.addClass("event-date");
            }
            // Set onClick handler for clicking a date
            curr_date.click({events: events, month: months[month], day:day}, date_click);
            row.append(curr_date);
        }
    }
    // Append the last row and set the current year
    calendar_days.append(row);
    $(".year").text(year);
}

// Get the number of days in a given month/year
function days_in_month(month, year) {
    var monthStart = new Date(year, month, 1);
    var monthEnd = new Date(year, month + 1, 1);
    return (monthEnd - monthStart) / (1000 * 60 * 60 * 24);    
}

// Event handler for when a date is clicked
function date_click(event) {
    $(".events-container").show(250);
    $("#dialog").hide(250);
    $(".active-date").removeClass("active-date");
    $(this).addClass("active-date");
    show_events(event.data.events, event.data.month, event.data.day);
};

// Event handler for when a month is clicked
function month_click(event) {
    $(".events-container").show(250);
    $("#dialog").hide(250);
    var date = event.data.date;
    $(".active-month").removeClass("active-month");
    $(this).addClass("active-month");
    var new_month = $(".month").index(this);
    date.setMonth(new_month);
    init_calendar(date);
}

// Event handler for when the year right-button is clicked
function next_year(event) {
    $("#dialog").hide(250);
    var date = event.data.date;
    var new_year = date.getFullYear()+1;
    $("year").html(new_year);
    date.setFullYear(new_year);
    init_calendar(date);
}

// Event handler for when the year left-button is clicked
function prev_year(event) {
    $("#dialog").hide(250);
    var date = event.data.date;
    var new_year = date.getFullYear()-1;
    $("year").html(new_year);
    date.setFullYear(new_year);
    init_calendar(date);
}

// Event handler for clicking the new event button
function new_event(event) {
    // if a date isn't selected then do nothing
    if($(".active-date").length===0)
        return;
    // remove red error input on click
    $("input").click(function(){
        $(this).removeClass("error-input");
    })
    // empty inputs and hide events
    $("#dialog input[type=text]").val('');
    $("#dialog input[type=number]").val('');
    $(".events-container").hide(250);
    $("#dialog").show(250);
    // Event handler for cancel button
    $("#cancel-button").click(function() {
        $("#name").removeClass("error-input");
        $("#count").removeClass("error-input");
        $("#dialog").hide(250);
        $(".events-container").show(250);
    });
    // Event handler for ok button
    $("#ok-button").unbind().click({date: event.data.date}, function() {
       
        var date = event.data.date;
        var name = $("#name").val().trim();
        var count = parseInt($("#phone").val().trim());
        var phone = parseInt($("#phone").val().trim());
        var time = $("#booktime").val().trim();
        var treat = $("#treat").val().trim();
        var doctor = $("#doctor").val().trim();
        var dayy = parseInt($(".active-date").html());
        if(dayy.toString().length == 1){
          var day = '0'+dayy;
        }
        else{
            var day = dayy;
        }
        var monthh = date.getMonth()+1;
        if(monthh.toString().length == 1){
          var month = '0'+monthh;
        }
        else{
            var month = monthh;
        }
        var year = date.getFullYear();
        var fulldate = year+'-'+month+'-'+day;
        // alert(name+phone+treat+doctor+time+year+month+day);
        // Basic form validation
        if(name.length === 0) {
            $("#name").addClass("error-input");
        }
        else if(isNaN(count)) {
            $("#count").addClass("error-input");
        }
        else {
            $("#dialog").hide(250);
            // console.log("new event");
            new_event_json(name, count, date, dayy);
            date.setDate(day);
            init_calendar(date);
        }

     axios
        .post("http://backend.victoriousskinclinic.com/api/appointment", {
          patient_name: name, 
            date: fulldate,
            doctor_name : doctor,
            time : time,
            phone :phone,
            patient_status: 0,
        })
        .then((response) => {
          alert('success');
        });
    // },

    });
}

// Adds a json event to event_data
function new_event_json(name, count, date, day) {
    var event = {
        "occasion": name,
        "invited_count": count,
        "year": date.getFullYear(),
        "month": date.getMonth()+1,
        "day": day
    };
    event_data["events"].push(event);
}

// Display all events of the selected date in card views
function show_events(events, month, day) {
    // Clear the dates container
    $(".events-container").empty();
    $(".events-container").show(250);
    // console.log(event_data["events"]);
    // If there are no events for this date, notify the user
    if(events.length===0) {
        var event_card = $("<div class='event-card'></div>");
        var event_name = $("<div class='event-name'>There are no events planned for "+month+" "+day+".</div>");
        $(event_card).css({ "border-left": "10px solid #FF1744" });
        $(event_card).append(event_name);
        $(".events-container").append(event_card);
    }
    else {
        // Go through and add each event as a card to the events container
        for(var i=0; i<events.length; i++) {
            var event_card = $("<div class='event-card'></div>");
            var event_name = $("<div class='event-name'>"+events[i]["occasion"]+":</div>");
            var event_count = $("<div class='event-count'>"+events[i]["invited_count"]+" Invited</div>");
            if(events[i]["cancelled"]===true) {
                $(event_card).css({
                    "border-left": "10px solid #FF1744"
                });
                event_count = $("<div class='event-cancelled'>Cancelled</div>");
            }
            $(event_card).append(event_name).append(event_count);
            $(".events-container").append(event_card);
        }
    }
}

// Checks if a specific date has any events
function check_events(day, month, year) {
    var events = [];
    for(var i=0; i<event_data["events"].length; i++) {
        var event = event_data["events"][i];
        if(event["day"]===day &&
            event["month"]===month &&
            event["year"]===year) {
                events.push(event);
                // alert('succcessfule');
            }
    }
    return events;
}



const months = [ 
    "January", 
    "February", 
    "March", 
    "April", 
    "May", 
    "June", 
    "July", 
    "August", 
    "September", 
    "October", 
    "November", 
    "December" 
];
    },
    
}

</script>

<style>
.booking{
  background-image: url(../assets/img/PINK.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.contentcalendar {
  overflow: none;
  max-width: 1000px;
  padding: 0px 0;
  height: 500px;
  position: relative;
  margin: 20px auto;
  background: #ffbef740;
  /* background: -moz-linear-gradient(right,  #ffbef7 0%, #ffbef7 80%, #9200fb 100%);
  background: -webkit-linear-gradient(right,  #ffbef7 0%, #ffbef7 80%, #9200fb 100%);
  background: linear-gradient(to left,  #ffbef7 0%, #ffbef7 80%, #9200fb 100%);     */
  border-radius: 3px;
  box-shadow: 3px 8px 16px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  -moz-box-shadow: 3px 8px 16px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  -webkit-box-shadow: 3px 8px 16px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}


.search {
  width: 50%;
      margin-bottom: auto;
    margin-top: 10px;
    height: 50px;
        background-color: #ffbef783;
    padding: 10px;
    border-radius: 5px;
}

.search-input {
    color: white;
    border: 0;
    outline: 0;
    background: none;
    width: 0;
    margin-top: 5px;
    caret-color: transparent;
    line-height: 20px;
    transition: width 0.4s linear
}

.search .search-input {
    padding: 0 10px;
    width: 100%;
    caret-color: #FF5D95;
    font-size: 19px;
    font-weight: 300;
    color: black;
    transition: width 0.4s linear;
}


.search-icon {
        height: 34px;
    width: 34px;
    float: right;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    background-color: #FF5D95;
    font-size: 10px;
        bottom: 30px;
        position: relative;
        border-radius: 5px;
}

.search-icon:hover{

  color: #fff !important;
}

a:link {
    text-decoration: none
}

.card-inner:hover{

   transform: scale(1.1);

}

.mg-text span{

  font-size: 12px;

}

.mg-text{

  line-height: 14px;
}
</style>