<template>
    <div>
        <Nav></Nav>
         <section id="book-a-table" class="book-a-table  booking">
            <div class="container">
                <div class="mt-5">

            <div class="wrapper">
        <!-- Sidebar  -->
        
        <nav id="sidebar">
            <h3 class="text-danger">Treatments</h3>
            <ul class="mainmenu" v-for="treat in treatments">
    <li><a href="">{{treat.name}}</a>
      <ul class="submenu"  v-for="treat_unit in treat.treatment_units">
        <li><a href="">{{treat_unit.name}}</a></li>
        <!-- <li><a href="">Bottoms</a></li>
        <li><a href="">Footwear</a></li> -->
      </ul>
    </li>
    </ul>

        </nav>
          
             
             <div class="card-body">
                <div class="row">
                    <div class="col-md-6">
                        <h1 class="detailtreatment"> Ance Treatment </h1>
                        <h3 class="text-center">Extraction &  Mask</h3>
                        <h5 class="text-center mt-3">This is treatment description.</h5>
                        <div class="row text-center mt-4">
                            <div class="offset-md-1 col-md-4">
                                <strong>1 Time </strong>
                            </div>
                            <div class="col-md-2">=></div>
                            <div class="col-md-4">
                                <strong>20000 MMK</strong>
                            </div>
                        </div>
                        <div class="row text-center mt-4">
                            <div class="offset-md-1 col-md-4">
                                <strong>2 Time </strong>
                            </div>
                            <div class="col-md-2">=></div>
                            <div class="col-md-4">
