</template>

<script>
import Nav from '@/components/Nav.vue';
import Footer from '@/components/Footer.vue';
import axios from 'axios'

    export default {
    name: "Treatment",
    components: { Footer,Nav } ,
     data(){
        return{
           treatments : null,
        }
        },
    mounted(){
        let treatment_id = this.$route.params.id;

      axios.get('http://backend.victoriousskinclinic.com/api/treatment')
        .then(res=>res.data.data)
        .then(data=>{

          if(treatment_id == 1){
            this.treatments = data.filter(x => x.body_part == 'Face');
            console.log('Face Treatment')
            console.log(this.treatments);
          }
          else if(treatment_id == 2){
            this.treatments = data.filter(x => x.body_part == 'Body');
            console.log('Body Treatment')
            console.log(this.treatments);
          }
          else{
            this.treatments = data.filter(x => x.body_part == 'Body Injection');
            console.log('Body Injection Treatment')
            console.log(this.treatments);
          }

        })
        
        $(document).ready(function () {
        
            $('#sidebarCollapse').on('click', function () {
                $('#sidebar').toggleClass('active');
            });
        });
    }
}
</script>

